@font-face {
  font-family: circular-book;
  src: url(../../assets/fonts/CircularStd-Book.otf);
}

@font-face {
  font-family: circular-bold;
  src: url(../../assets/fonts/CircularStd-Bold.otf);
}

@font-face {
  font-family: circular-black;
  src: url(../../assets/fonts/CircularStd-Black.otf);
}


.details {
  display: -ms-flexbox;
  display: flex;
  position: absolute;
}

.song-details {
  padding-left: 10px;
  line-height: 20px;
}

.song-details .song-name {
  font-family: circular-bold;
  color: #fff;
  font-size: 1rem;
}

.song-details .artist-name {
  font-family: circular-bold;
  color: #aaa;
  font-size: 0.8rem;
}

.song-controls {
  -ms-flex-pack: center;
      justify-content: center;
  display: -ms-flexbox;
  display: flex;
  margin-top: 20px;
}

.forward,
.reverse,
.play-btn,
.pause-btn {
  padding: 5px 20px;
  color: rgb(179, 179, 179);
  cursor: pointer;
  height: 40px;
}

.reverse,
.forward {
  font-size: 16px;
  padding-top: 4  px
}

.song-player-container .play-btn ,.pause-btn {
  position: relative;
  top: -7px;
  font-size: 40px;
}



.play-btn .fa {
  -webkit-text-stroke: 4px rgb(40, 40, 40);
}

.song-progress-container {
  margin-top: -10px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.song-progress {
  width: 500px;
  height: 4px;
  background: rgb(64, 64, 64);;
  border-radius: 4px;
}

.song-expired {
  margin: 0;
  width: 100px;
  height: 4px;
  background: #1db954;
  border-radius: 4px;
}

.timer-start {
  font-size: 12px;
  position: relative;
  top: -5px;
  left: -10px;
}

.timer-end {
  font-size: 12px;
  position: relative;
  top: -5px;
  left: 10px;
}



